// src/App.js
import React, { useState, useEffect, lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ErrorBoundary as ReactErrorBoundary } from 'react-error-boundary';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Navbar from './components/common/Navbar';
import Loading from './components/common/Loading';
import ErrorBoundary from './components/common/ErrorBoundary';
import './App.css';

// Lazy loading para componentes não críticos
const Home = lazy(() => import('./components/layout/Home'));
const Dashboard = lazy(() => import('./components/layout/Dashboard'));
const PrivateRoute = lazy(() => import('./components/common/PrivateRoute'));
const LoginReset = lazy(() => import('./pages/LoginReset'));
const AuthDiagnostic = lazy(() => import('./pages/AuthDiagnostic'));
const EmergencyReset = lazy(() => import('./pages/EmergencyReset'));

// Componente de fallback para carregamento de componentes assíncronos
const LazyLoadingFallback = () => <Loading message="Carregando componente..." />;

// Componente para exibir erros de forma amigável
const ErrorFallback = ({ error, resetErrorBoundary }) => (
  <div className="error-container">
    <h1>Algo deu errado</h1>
    <p>Um erro inesperado ocorreu na aplicação.</p>
    <p className="error-message">{error.message}</p>
    <button 
      className="retry-button"
      onClick={resetErrorBoundary}
    >
      Tentar Novamente
    </button>
  </div>
);

// Componente principal da aplicação
function AppContent() {
  const { user, isAuthenticated, isLoading, login, logout, error } = useAuth();
  const [networkStatus, setNetworkStatus] = useState(navigator.onLine);
  const [offlineMode, setOfflineMode] = useState(false);
  
  // Monitor network status
  useEffect(() => {
    const handleOnline = () => setNetworkStatus(true);
    const handleOffline = () => {
      setNetworkStatus(false);
      setOfflineMode(true);
    };
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);
  
  // Renderiza a tela de carregamento enquanto verifica autenticação
  if (isLoading) {
    return <Loading message="Verificando autenticação..." />;
  }
  
  // Renderizar tela de login se não estiver autenticado
  if (!isAuthenticated) {
    return (
      <div style={{ 
        display: 'flex', 
        flexDirection: 'column', 
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh',
        padding: '20px'
      }}>
        <h1>AdamChat - Login</h1>
        <p>Você não está autenticado. Por favor, faça login para continuar.</p>
        
        {error && (
          <div style={{ 
            color: 'red', 
            backgroundColor: '#ffeeee', 
            padding: '10px', 
            borderRadius: '4px',
            marginBottom: '20px',
            width: '100%',
            maxWidth: '400px',
            textAlign: 'center'
          }}>
            {error}
          </div>
        )}
        
        <div style={{ marginTop: '20px', display: 'flex', flexDirection: 'column', gap: '10px' }}>
          <button 
            onClick={() => {
              const username = prompt('Digite seu nome de usuário:');
              const password = prompt('Digite sua senha:');
              if (username && password) {
                login(username, password);
              }
            }}
            style={{ 
              padding: '10px 20px', 
              fontSize: '16px', 
              cursor: 'pointer',
              backgroundColor: '#4CAF50',
              color: 'white',
              border: 'none',
              borderRadius: '4px'
            }}
          >
            Login
          </button>
          
          <button 
            onClick={() => {
              localStorage.clear();
              sessionStorage.clear();
              window.location.reload();
            }}
            style={{ 
              padding: '10px 20px', 
              fontSize: '16px', 
              cursor: 'pointer',
              backgroundColor: '#f44336',
              color: 'white',
              border: 'none',
              borderRadius: '4px'
            }}
          >
            Limpar Armazenamento
          </button>
        </div>
      </div>
    );
  }
  
  // Renderiza notificação de modo offline se aplicável
  const OfflineBanner = () => offlineMode && (
    <div className="offline-banner">
      <span className="offline-icon">⚠️</span>
      <span>Modo offline ativo - Algumas funcionalidades podem estar indisponíveis</span>
    </div>
  );
  
  return (
    <div className="App">
      <OfflineBanner />
      <Navbar />
      <div className="content">
        <Suspense fallback={<LazyLoadingFallback />}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route 
              path="/dashboard/*" 
              element={
                <PrivateRoute>
                  <Dashboard />
                </PrivateRoute>
              } 
            />
            <Route path="/login-reset" element={<LoginReset />} />
            <Route path="/auth-diagnostic" element={<AuthDiagnostic />} />
            <Route path="/emergency-reset" element={<EmergencyReset />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Suspense>
      </div>
      <footer className="app-footer">
        <p>&copy; {new Date().getFullYear()} AdamChat - Todos os direitos reservados</p>
        {process.env.NODE_ENV === 'development' && (
          <p className="dev-mode">Modo de Desenvolvimento</p>
        )}
      </footer>
    </div>
  );
}

// Componente App que envolve a aplicação com o provedor de autenticação
function App() {
  return (
    <ReactErrorBoundary FallbackComponent={ErrorFallback} onReset={() => window.location.reload()}>
      <AuthProvider>
        <Router>
          <ErrorBoundary>
            <AppContent />
          </ErrorBoundary>
        </Router>
      </AuthProvider>
    </ReactErrorBoundary>
  );
}

export default App;
