// src/index.js
// Apply MUI patches first (before any other imports)
import './patches/patchMUI';
import './mui-fix';
import './mui-override.css';

import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { CircularProgress, Box, Typography } from '@mui/material';
import { AuthProvider } from './contexts/AuthContext';

// Verificar se cookies estão habilitados
const areCookiesEnabled = () => {
  try {
    document.cookie = "testcookie=1; SameSite=Strict; Secure";
    const cookieEnabled = document.cookie.indexOf("testcookie") !== -1;
    document.cookie = "testcookie=; expires=Thu, 01 Jan 1970 00:00:00 GMT; SameSite=Strict; Secure";
    console.log("Cookies are enabled, proceeding with initialization");
    return cookieEnabled;
  } catch (e) {
    console.error("Error checking cookie status:", e);
    return false;
  }
};

console.log("index.js: Initializing application");

// Verificar se estamos em localhost e redirecionar para 127.0.0.1 para consistência de cookies
if (window.location.hostname === 'localhost' && window.location.port === '3000') {
  window.location.replace(`http://127.0.0.1:3000${window.location.pathname}${window.location.search}`);
}

// Verificar se a URL contém [object Promise] e corrigir
if (window.location.href.includes('[object') || window.location.href.includes('Promise')) {
  window.location.replace(window.location.origin);
}

// Componente de carregamento enquanto a aplicação inicializa
const LoadingComponent = () => (
  <Box
    sx={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      height: '100vh',
      bgcolor: '#f8fafc'
    }}
  >
    <CircularProgress size={60} sx={{ color: '#2563eb' }} />
    <Typography variant="h6" sx={{ mt: 2, color: '#1e293b' }}>
      Carregando AdamChat...
    </Typography>
  </Box>
);

// Renderizar a aplicação
console.log("Using React 18 rendering API");
try {
  const container = document.getElementById('root');
  const root = ReactDOM.createRoot(container);
  
  root.render(
    <React.StrictMode>
      <AuthProvider>
        <BrowserRouter>
          <App />
        </BrowserRouter>
      </AuthProvider>
    </React.StrictMode>
  );
} catch (e) {
  console.error('Error rendering React application:', e);
}

// Monitorar performance
reportWebVitals();
