import React, { createContext, useState, useEffect, useContext } from 'react';
import authService from '../services/auth-service';

// Criar o contexto de autenticação
const AuthContext = createContext(null);

// Hook personalizado para usar o contexto de autenticação
export const useAuth = () => useContext(AuthContext);

// Provedor de autenticação
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Verificar autenticação ao inicializar
  useEffect(() => {
    const checkAuth = async () => {
      try {
        setLoading(true);
        
        // Verificar se há um token armazenado
        const token = authService.getToken();
        
        if (token) {
          // Validar o token com o backend
          const result = await authService.validateAuthentication();
          
          if (result.authenticated) {
            setUser(result.user);
            setIsAuthenticated(true);
          } else {
            // Se o token não for válido, tentar atualizar
            const refreshed = await authService.refreshToken();
            
            if (refreshed) {
              // Verificar novamente após atualizar o token
              const newResult = await authService.validateAuthentication();
              
              if (newResult.authenticated) {
                setUser(newResult.user);
                setIsAuthenticated(true);
              } else {
                // Se ainda não for válido, limpar a sessão
                authService.clearSession();
                setUser(null);
                setIsAuthenticated(false);
              }
            } else {
              // Se não conseguir atualizar, limpar a sessão
              authService.clearSession();
              setUser(null);
              setIsAuthenticated(false);
            }
          }
        } else {
          // Se não houver token, o usuário não está autenticado
          setUser(null);
          setIsAuthenticated(false);
        }
      } catch (err) {
        console.error('Erro ao verificar autenticação:', err);
        setError(err);
        setUser(null);
        setIsAuthenticated(false);
      } finally {
        setLoading(false);
      }
    };
    
    checkAuth();
  }, []);

  // Função de login
  const login = async (credentials) => {
    try {
      setLoading(true);
      setError(null);
      
      const result = await authService.login(credentials);
      
      if (result.success) {
        setUser(result.user);
        setIsAuthenticated(true);
        return { success: true };
      } else {
        setError(result.message);
        return { success: false, message: result.message };
      }
    } catch (err) {
      console.error('Erro ao fazer login:', err);
      setError(err.message || 'Erro ao fazer login');
      return { success: false, message: err.message || 'Erro ao fazer login' };
    } finally {
      setLoading(false);
    }
  };

  // Função de registro
  const register = async (userData) => {
    try {
      setLoading(true);
      setError(null);
      
      const result = await authService.register(userData);
      
      if (result.success) {
        return { success: true, user: result.user };
      } else {
        setError(result.message);
        return { success: false, message: result.message };
      }
    } catch (err) {
      console.error('Erro ao registrar usuário:', err);
      setError(err.message || 'Erro ao registrar usuário');
      return { success: false, message: err.message || 'Erro ao registrar usuário' };
    } finally {
      setLoading(false);
    }
  };

  // Função de logout
  const logout = async () => {
    try {
      setLoading(true);
      
      await authService.logout();
      
      setUser(null);
      setIsAuthenticated(false);
      
      return { success: true };
    } catch (err) {
      console.error('Erro ao fazer logout:', err);
      return { success: false, message: err.message };
    } finally {
      setLoading(false);
    }
  };

  // Verificar se o usuário tem um papel específico
  const hasRole = (role) => {
    if (!user || !user.roles) return false;
    return user.roles.includes(role);
  };

  // Verificar se o usuário é admin
  const isAdmin = () => hasRole('admin');

  // Valor do contexto
  const value = {
    user,
    loading,
    error,
    isAuthenticated,
    login,
    register,
    logout,
    hasRole,
    isAdmin
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthContext; 