/**
 * Serviço de Autenticação
 * 
 * Fornece funcionalidades para:
 * - Gerenciamento de sessão
 * - Verificação de autenticação
 * - Recuperação de informações do usuário
 * - Transição para o modo de emergência
 */

import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
const AUTH_TOKEN_KEY = 'auth_token';
const REFRESH_TOKEN_KEY = 'refresh_token';
const USER_INFO_KEY = 'user_info';

class AuthService {
  /**
   * Verifica o status do backend e do sistema de autenticação
   * Útil para diagnóstico e decisão de fallback para o modo de emergência
   */
  async checkAuthStatus() {
    try {
      // Verificar status do sistema de autenticação no backend
      const response = await axios.get(`${API_URL}/auth/status`);
      return response.data;
    } catch (error) {
      console.error('Erro ao verificar status da autenticação:', error);
      return {
        status: 'error',
        message: 'Não foi possível verificar o status da autenticação',
        emergency_mode: false
      };
    }
  }
  
  /**
   * Verifica se o usuário está autenticado diretamente com o backend
   * Útil para validar tokens e obter informações atualizadas do usuário
   */
  async validateAuthentication() {
    try {
      // Obter token atual
      const token = this.getToken();
      
      if (!token) {
        return { authenticated: false };
      }
      
      // Verificar com o backend
      const response = await axios.get(`${API_URL}/auth/check`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erro ao validar autenticação:', error);
      
      // Se o erro for 401, o token expirou ou é inválido
      if (error.response && error.response.status === 401) {
        // Tentar atualizar o token
        const refreshed = await this.refreshToken();
        if (refreshed) {
          // Tentar novamente com o novo token
          return this.validateAuthentication();
        }
      }
      
      return { authenticated: false };
    }
  }
  
  /**
   * Obtém token de autenticação atual
   */
  getToken() {
    return localStorage.getItem(AUTH_TOKEN_KEY);
  }
  
  /**
   * Obtém token de refresh atual
   */
  getRefreshToken() {
    return localStorage.getItem(REFRESH_TOKEN_KEY);
  }
  
  /**
   * Obtém informações sobre o usuário autenticado
   */
  getUserInfo() {
    const userInfoStr = localStorage.getItem(USER_INFO_KEY);
    if (userInfoStr) {
      try {
        return JSON.parse(userInfoStr);
      } catch (e) {
        console.error('Erro ao parsear informações do usuário:', e);
        return null;
      }
    }
    return null;
  }
  
  /**
   * Verifica se o usuário tem um papel específico
   */
  hasRole(role) {
    const userInfo = this.getUserInfo();
    if (userInfo && userInfo.roles) {
      return userInfo.roles.includes(role);
    }
    return false;
  }
  
  /**
   * Verifica se o usuário é um administrador
   */
  isAdmin() {
    return this.hasRole('admin');
  }
  
  /**
   * Atualiza o token de autenticação
   */
  async refreshToken() {
    try {
      const refreshToken = this.getRefreshToken();
      
      if (!refreshToken) {
        return false;
      }
      
      const response = await axios.post(`${API_URL}/auth/refresh`, {
        refresh_token: refreshToken
      });
      
      if (response.data.status === 'success' && response.data.tokens) {
        // Armazenar novos tokens
        this.setSession(
          response.data.tokens.access_token,
          response.data.tokens.refresh_token
        );
        return true;
      }
      
      return false;
    } catch (error) {
      console.error('Erro ao atualizar token:', error);
      // Se o refresh token expirou, fazer logout
      if (error.response && error.response.status === 401) {
        this.logout();
      }
      return false;
    }
  }
  
  /**
   * Realiza o login do usuário
   */
  async login(credentials) {
    try {
      const response = await axios.post(`${API_URL}/auth/login`, credentials);
      
      if (response.data.status === 'success' && response.data.tokens) {
        // Armazenar tokens e informações do usuário
        this.setSession(
          response.data.tokens.access_token,
          response.data.tokens.refresh_token,
          response.data.user
        );
        return {
          success: true,
          user: response.data.user
        };
      }
      
      return {
        success: false,
        message: response.data.message || 'Erro ao realizar login'
      };
    } catch (error) {
      console.error('Erro ao realizar login:', error);
      return {
        success: false,
        message: error.response?.data?.message || 'Erro ao realizar login'
      };
    }
  }
  
  /**
   * Realiza o registro de um novo usuário
   */
  async register(userData) {
    try {
      const response = await axios.post(`${API_URL}/auth/register`, userData);
      
      if (response.data.status === 'success') {
        return {
          success: true,
          user: response.data.user
        };
      }
      
      return {
        success: false,
        message: response.data.message || 'Erro ao registrar usuário'
      };
    } catch (error) {
      console.error('Erro ao registrar usuário:', error);
      return {
        success: false,
        message: error.response?.data?.message || 'Erro ao registrar usuário'
      };
    }
  }
  
  /**
   * Realiza o logout do usuário
   */
  async logout() {
    try {
      const refreshToken = this.getRefreshToken();
      
      if (refreshToken) {
        // Informar o backend sobre o logout
        await axios.post(`${API_URL}/auth/logout`, {
          refresh_token: refreshToken
        }).catch(e => console.error('Erro ao notificar logout no servidor:', e));
      }
      
      // Limpar dados de sessão localmente
      this.clearSession();
      
      return {
        success: true,
        message: 'Logout realizado com sucesso'
      };
    } catch (error) {
      console.error('Erro ao realizar logout:', error);
      
      // Mesmo com erro, limpar dados locais
      this.clearSession();
      
      return {
        success: true,
        message: 'Logout realizado com sucesso (local)'
      };
    }
  }
  
  /**
   * Armazena os dados da sessão
   */
  setSession(token, refreshToken, userInfo = null) {
    localStorage.setItem(AUTH_TOKEN_KEY, token);
    localStorage.setItem(REFRESH_TOKEN_KEY, refreshToken);
    
    if (userInfo) {
      localStorage.setItem(USER_INFO_KEY, JSON.stringify(userInfo));
    }
    
    // Configurar interceptor para adicionar token às requisições
    this.setupAxiosInterceptors();
  }
  
  /**
   * Limpa os dados da sessão
   */
  clearSession() {
    localStorage.removeItem(AUTH_TOKEN_KEY);
    localStorage.removeItem(REFRESH_TOKEN_KEY);
    localStorage.removeItem(USER_INFO_KEY);
  }
  
  /**
   * Cria um cabeçalho de autorização com o token atual
   * Útil para requisições que exigem autenticação
   */
  authHeader() {
    const token = this.getToken();
    return token ? { 'Authorization': `Bearer ${token}` } : {};
  }
  
  /**
   * Configura interceptadores de requisição para adicionar token
   * automaticamente às requisições e lidar com erros de autenticação
   */
  setupAxiosInterceptors() {
    // Remover interceptadores anteriores
    axios.interceptors.request.eject(this.requestInterceptor);
    axios.interceptors.response.eject(this.responseInterceptor);
    
    // Interceptor para adicionar token às requisições
    this.requestInterceptor = axios.interceptors.request.use(
      (config) => {
        // Se a requisição já tiver Authorization, não substitui
        if (config.headers && config.headers.Authorization) {
          return config;
        }
        
        // Se é uma requisição para uma API que requer autenticação
        if (config.url.includes('/api/') && !config.url.includes('/auth/login') && !config.url.includes('/auth/register')) {
          const token = this.getToken();
          if (token) {
            config.headers.Authorization = `Bearer ${token}`;
          }
        }
        
        return config;
      },
      (error) => Promise.reject(error)
    );
    
    // Interceptor para lidar com erros de autenticação
    this.responseInterceptor = axios.interceptors.response.use(
      (response) => response,
      async (error) => {
        // Se o erro for 401 (Unauthorized) e não for uma requisição de login/refresh
        if (error.response && error.response.status === 401 &&
            !error.config.url.includes('/auth/login') &&
            !error.config.url.includes('/auth/refresh')) {
          
          // Tentar atualizar o token
          const refreshed = await this.refreshToken();
          
          if (refreshed) {
            // Reenviar a requisição original com o novo token
            const token = this.getToken();
            error.config.headers.Authorization = `Bearer ${token}`;
            return axios(error.config);
          } else {
            // Se não conseguir atualizar o token, fazer logout
            this.logout();
          }
        }
        
        return Promise.reject(error);
      }
    );
  }
}

// Exportar uma instância única do serviço
const authService = new AuthService();
export default authService; 